* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
.wrapper {
  padding-top: 50px;
  background: gray;
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

.brand {
 width: 33.3%;
  background-color: darkkhaki;
  text-align: center;
  color: white;
}
.row {
  height: 70px;
}




.title {
 
  text-align: center;
  font-size: 3rem;
  text-decoration: underline;
  color: #2A77AE;
  margin-bottom: 100px;
 
}
 .jumbotron {
   
   background-color: green;
   color: white;
   text-align: center;
   width: 100%;
 }

 .line {
   text-align: center;
   width: 100%;
 }

 .row {
   width: 100%;
    color: white;
   text-align: center;
 }

 .header {
   background-color: green;
 }

.card {
  background: #fff;
  border-radius: 2px;
  height: 325px;
  margin: 1rem;
  position: relative;
  width: 325px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:#6CADDC;
}
.card > .img-container > img {
  width: 100%
}



